@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600&display=swap");
:root {
  --fade-color-1: #0061ff;
  --fade-color-2: #5ae2f1;

}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;

}

.logo{
    height: 70px;
    width: 70px;
}
.header{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: auto;
    padding-left: 50px;
    padding-right: 50px;
    z-index: 999;
}

.home-banner{
  
    background-image: linear-gradient( 0.25turn, rgba(0, 97, 225, 0.3), rgba(90, 226, 241, 0.3)), url("../images//bg-home-banner.jpg");
 
    background-size: cover;
    /* position: relative; */
    height: 580px;
display: block;
  }
  @media only screen and (max-width: 768px) {
    .home-banner{
  
        background-image: linear-gradient( 0.25turn, rgba(0, 97, 225, 0.3), rgba(90, 226, 241, 0.3)), url("../images//bg-home-banner.jpg");
     
        background-size: cover;
        /* position: relative; */
        height: 470px;
    
      }
  }
.banner-text{
    text-align: center;
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    font-size: 45px;
    text-transform: uppercase;
    text-align: justify;
}
@media only screen and (max-width: 768px) {
    .banner-text{
        text-align: center;
        position: relative;
        top: 330px;
        left: 50%;
        transform: translate(-50%, -50%);
        color: white;
        font-size: 27px;
    }
  }
  
.navbar-nav .nav-link{
    color: white !important;
    font-size: 20px;
}
.baner-text2{
    padding-top: 120px;
}
.main{
    height: 100%;
}
.heading{
    font-weight: 400;
    background-image: -webkit-linear-gradient(
0turn
, var(--fade-color-1), var(--fade-color-2));
    -webkit-background-clip: text;background-clip:text;
    -webkit-text-fill-color: transparent;
}
.calender-bg{
    font-weight: 400;
border-radius: 5px;
border-color: var(--fade-color-1) !important;
    
}
.event-heading{
    /* font-weight: 300px; */
    font-size: 25px;
    background-image: -webkit-linear-gradient(
0turn
, var(--fade-color-1), var(--fade-color-2));
    -webkit-background-clip: text;background-clip:text;
    -webkit-text-fill-color: transparent;
}
.content{
    font-size: 18px;
    text-align: justify;
}
.banner-svg{
    position: absolute;
    top: 517px;
}
@media only screen and (max-width: 768px) {
    .banner-svg{
        position: absolute;
        top: 450px;
    }
  }
  .partner-list{
      list-style: none;
  }
  .social-icon{
    background-image: -webkit-linear-gradient(
        0turn
        , var(--fade-color-1), var(--fade-color-2));
            -webkit-background-clip: text;background-clip:text;
            -webkit-text-fill-color: transparent;
            font-size: 30px;
            padding: 20px;
            cursor: pointer;
      
  }
