.five > .carousel-root > .carousel {
    background-image: -webkit-linear-gradient(
        0turn
        , var(--fade-color-1), var(--fade-color-2));
         
           
    -webkit-backdrop-filter: blur(15px);
         
           
            backdrop-filter: blur(15px);
    top: -115px !important;
    height: 202px;
    display: flex;
    justify-content: center;
}
.five > .carousel-root > .carousel-slider {
    background: white;
    -webkit-backdrop-filter: blur(0);
            backdrop-filter: blur(0);
    top: 0 !important;
    display: block;
    height: auto;
    height: 595px;
}
.five > .carousel-root {
    /* height: 100vh; */
    background-color: white;
}
