.thumbs-wrapper > .thumbs > .thumb > img {
    vertical-align: baseline;
    vertical-align: initial;
    height: 104px;
    width: 104px;

    border-radius: 50%;
}
.thumbs-wrapper > .thumbs > .thumb {
    width: 140px !important;
    border: none !important;
    margin: 0px 36px;
}
.carousel .slide img {
    width: 50% !important;
    object-fit: contain;
    height: 556px;
}
.thumb-img > img {
    vertical-align: top;
    border-radius: 50%;
    width: 76px;
    height: 76px;
}
