@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600&display=swap");
:root {
  --fade-color-1: #0061ff;
  --fade-color-2: #5ae2f1;

}


.five > .carousel-root > .carousel {
    background-image: -webkit-linear-gradient(
        0turn
        , var(--fade-color-1), var(--fade-color-2));
         
           
    backdrop-filter: blur(15px);
    top: -115px !important;
    height: 202px;
    display: flex;
    justify-content: center;
}
.five > .carousel-root > .carousel-slider {
    background: white;
    backdrop-filter: blur(0);
    top: 0 !important;
    display: block;
    height: auto;
    height: 595px;
}
.five > .carousel-root {
    /* height: 100vh; */
    background-color: white;
}
/* .five > .carousel-root > .carousel > .thumbs-wrapper {
    margin-top: 40px;
  } */
.thumbs-wrapper > .thumbs > .thumb > img {
    vertical-align: initial;
    height: 104px;
    width: 104px;

    border-radius: 50%;
}
.thumbs-wrapper > .thumbs > .thumb {
    width: 140px !important;
    border: none !important;
    margin: 0px 36px;
}
.carousel .slide img {
    width: 50% !important;
    object-fit: contain;
    height: 556px;
}
.thumb-img > img {
    vertical-align: top;
    border-radius: 50%;
    width: 76px;
    height: 76px;
}
.title-thumb {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 300;
    font-size: 24px;
    line-height: 28px;
    text-align: center;
    color:white;
}
.sub-thumb {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 300;
    font-size: 12px;
    line-height: 14px;
    text-align: center;
    color:white;
}
.carousel .thumb.selected,
.carousel .thumb:hover {
    /* border: 3px solid hsl(240deg 6% 87%); */
    height: 135px;
}
.thumb-img {
    display: flex;
    justify-content: center;
}
.caro {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 300;
    font-size: 60px;
    line-height: 70px;

    background-image: -webkit-linear-gradient(
        0turn
        , var(--fade-color-1), var(--fade-color-2));
            -webkit-background-clip: text;background-clip:text;
            -webkit-text-fill-color: transparent;
}
@media only screen and (max-width: 786px) {
    .caro {
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 300;
        font-size: 30px;
        line-height: 30px;
    
        background-image: -webkit-linear-gradient(
            0turn
            , var(--fade-color-1), var(--fade-color-2));
                -webkit-background-clip: text;background-clip:text;
                -webkit-text-fill-color: transparent;
    }
  }
.carosub {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 300;
    font-size: 24px;
    line-height: 28px;

    color: #4b176a;
}
.carodetail {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
    text-align: justify;

    color: #414441;
}
.teamHeadingMain {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: bold;
    font-size: 40px;
    line-height: 47px;
    text-align: center;
    background: #fff;
    color: #4b176a;
}
/* .five {
    margin-bottom: 120px;
} */
.carousel .control-prev.control-arrow:before {
    border-right: 20px solid  #0a5fe7;
       
    
        

}
.carousel .control-arrow:before,
.carousel.carousel-slider .control-arrow:before {
    margin: 0 5px;
    display: inline-block;
    border-top: 20px solid transparent;
    border-bottom: 20px solid transparent;
    content: '';
}
.carousel .control-next.control-arrow:before {
    border-left: 20px solid #0a5fe7;
}
.carousel .control-arrow:before,
.carousel.carousel-slider .control-arrow:before {
    margin: 0 5px;
    display: inline-block;
    border-top: 20px solid transparent;
    border-bottom: 20px solid transparent;
    content: '';
}
.team-slider-img{
    height: 450px  !important;
    width: 450px !important;
}
@media only screen and (max-width: 425px) {
    .team-slider-img{
        height: 200px !important;
        width: 200px !important;
        margin-top: 150px;
    }
  }
  
  @media only screen and (max-width: 768px) {
.carousel{
      margin-top: 60px !important;
  }
  .carodetail{
      font-size: 12px;
      line-height: 15px;
  }
}